
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Scroll from 'assembly/scroll/scroll'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 20

export default {
components: {
XHeader,
Divider,
Tab,
TabItem,
Scroll,
Loading
},
data () {
return {
switchIndex: 0,
orderList: [],
listenScroll: true,
hasMore: true,
inited: false, // 是否在请求列表接口中
switchArr: [0, 1, 2, 6, 8, 7],
pageNo: 1
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
switchTabItem (index) {
this.switchIndex = index
this.pageNo = 1
this.orderList = []
this.hasMore = true
this._getOrder(this.switchArr[this.switchIndex])
},
loadMore () {
if (!this.hasMore) {
return
}
this.pageNo ++
this._getOrder(this.switchArr[this.switchIndex])
},
toDetail (orderNo) {
this.$router.push({
path: '/orderDetail',
query: {
orderNo: orderNo
}
})
},
_getOrder (index) {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.inited = true

let data = {
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
}
}
if (index !== 0) {
data.orderStatus = index
}
this.$tupHttp(this, 'front/order/getClientOrderList', 'POST', data).then(res => {
this.inited = false
if (index === this.switchArr[this.switchIndex]) {
this.orderList = this.orderList.concat(res.data.models.map((item) => {
item.createtime = new Date(item.createtime).format('yyyy-MM-dd hh:mm:ss')
return item
}))
if (res.data.models.length < PAGESIZE) { // 是否有下一页
this.hasMore = false
}
}
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
created () {
let i = this.$route.query.index || 0
this.switchIndex = Number(i)
this._getOrder(this.switchArr[Number(i)])
}
}



